.hide-social {
   padding: 5px;
   box-sizing: border-box;
   display: none;
   @include flex-wrap(wrap);
   @include flex-direction(row);
   @include justify-content(space-around);
   float: left;
   position: absolute;
   width: 100%;
   /*height: 100px;*/
   background-color: #090000;
   margin-top: -36px;
   left: 0;
   &:after{
     display: block;
     position: absolute;
     width: 0;
     height: 0;
     content: '';
     right: 4px;
     bottom: -10px;
     border-left: 5px solid transparent;
     border-right: 5px solid transparent;
     border-top: 10px solid #090000;
   }
   > a, span {
     width: 20px;
     height: 20px;
     display: block;
     color: #fff;
     font-size: 14px;
     font-weight: 400;
     float: left;
     position: relative;
     margin-right: 0;
     text-align: center;
     margin-top: 0;
     &:hover{
       i{
         color: #ee2e24;
       }
     }
   }
 }
