.pagination{
  position: relative;
  float: left;
  width: 100%;
  padding: 10px;
  @include box-sizing(border-box);
  ul{
    @include display-flex(flex);
    @include flex-wrap(wrap);
    @include flex-direction(row);
    @include justify-content(center);
    width: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
    li{
      float: left;
      a{
        position: relative;
        float: left;
     /*   width: 100%;
        height: 100%;*/
        padding: 10px 15px;
        @include box-sizing(border-box);
        text-align: center;
        color: #0c242e;
        font-size: 15px;
        font-weight: 700;
        &:hover{
          color: #ee2e24;
        }
      }
    }
    li.active{
      a{
        color: #ee2e24;
      }
    }
    li.pagination-prew{
      a{
        display: block;
        position: relative;
        float: left;
        width: 14px;
        height: 25px;
        margin-top: 9px;
        padding: 0;
        background-image: url('../img/icons/arrow-left.png');
        background-repeat: no-repeat;
        -webkit-background-size: contain;
        background-size: contain;
      }
    }
    li.pagination-next{
      a{
        display: block;
        position: relative;
        float: left;
        width: 14px;
        height: 25px;
        margin-top: 9px;
        padding: 0;
        background-image: url('../img/icons/arrow-right.png');
        background-repeat: no-repeat;
        -webkit-background-size: contain;
        background-size: contain;
      }
    }
  }
}
